import uniIcon from "@/components/uni-icons/uni-icons.vue";

let _this
export default {
	components:{
		uniIcon,
		
	},
	data() {
		return {
			companyCode:'',
			pictures:[]
		};
	},
	onShow:function(){
		this.getPictureList()
	},
	onLoad:function(options){
		_this = this
		console.log(options)
		this.companyCode = options.companyCode
		this.getPictureList()
	},
	methods:{
		
		getPictureList:function(){
			let url = _this.Api.slideshow
			let data ={
				companyCode: _this.companyCode,  // 公司编码 
			}
			this.myRequest(data,url,'get').then(res => {
			      console.log(res)
				  _this.pictures = res.data.data.idLabels
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		delPic:function(index){
			console.log('wfefe')
			let url = _this.Api.slideshowDel
			let data ={
				
				pictureCode: index         // 图片路径
			    
			}
			this.myRequest(data,url,'get').then(res => {
			      console.log(res)
				  if (res.data.status==0){
				  	  _this.getPictureList()
				  }
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		submit:function(picture){
			let url = _this.Api.slideshowAdd
			let data ={
				companyCode: _this.companyCode,  // 公司编码
				pictures: [picture]         // 图片数组

			}
			this.myRequest(data,url,'post').then(res => {
			      console.log(res)
				  if (res.data.status==0){
					  _this.getPictureList()
				  }
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		bindCancel:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		pickImage:function(){
					  uni.chooseImage({
						  sizeType: ['compressed'],
					      success: (chooseImageRes) => {
					          const tempFilePaths = chooseImageRes.tempFilePaths;
		                      _this.pictures = []
							  for (let i=0 ;i<tempFilePaths.length;i++){
								  uni.uploadFile({
								      url: _this.Api.upload, 
									  filePath: tempFilePaths[i],
									  name:'file',
								      success: (uploadFileRes) => {
								          console.log(uploadFileRes);	
										  // _this.pictures.push(JSON.parse(uploadFileRes.data).data.msg)
										  _this.submit(JSON.parse(uploadFileRes.data).data.msg)
										  uni.showToast({
										  	title: '上传成功',
											icon:'none',
											duration: 1000
										  });
								      }
								  });
							  }

					      }
					  });
		},
	}
}
